<template>
  <div class="user-detail">
    <div class="user-detail-top"></div>
    <div class="user-detail-group">
      <div class="user-icon">
        <span></span>
        <img src="./images/me_icon.png" alt="">
      </div>
      <div class="user-item">
        <span></span>
        <span>189****87892</span>
      </div>
      <div class="user-item">
        <span></span>
        <span><input type="text"></span>
      </div>
      <div class="user-item" @click="sheetVisible = true">
        <span></span>
        <span>{{user_sex}}</span>
      </div>
      <div class="user-item">
        <span></span>
        <span><input type="text"></span>
      </div>
      <div class="user-item" @click="$refs.picker.open()">
        <span></span>
        <span>{{user_birthday}}</span>
      </div>
      <div class="user-item">
        <span></span>
        <span><input type="text"></span>
      </div>
    </div>
    <button @click="saveUserInfo"></button>
    <!---->
    <mt-actionsheet
      :actions="actions"
      v-model="sheetVisible">
    </mt-actionsheet>
    <!---->
    <mt-datetime-picker
      ref="picker"
      type="date"
      year-format="{value} "
      month-format="{value} "
      date-format="{value} "
      :startDate=startDate
      :endDate=endDate
      @confirm="handleBirthday"
    >
    </mt-datetime-picker>

  </div>
</template>

<script>
  import moment from 'moment';
  import {mapState} from 'vuex'
  import {changeUserInfo} from './../../api/index'
  import {Toast} from  'mint-ui';
  export default {
    name: "UserDetail",
    data(){
      return {
        // 0.
        user_sex: '',
        user_birthday: '',

         // 1. 
        actions: [
          {name: "", method: this.selectSex},
          {name: "", method: this.selectSex},
        ],
        sheetVisible: false,

        // 2. 
        pickerVisible: false,
        startDate: new Date('1960-01-01'),
        endDate: new Date('1970-01-01')
      }
    },
    methods:{
      selectSex(props){
        console.log(props);
        this.user_sex = props.name;
      },
      handleBirthday(data){
          this.user_birthday =  moment(data).format('YYYYMMDD');
      },
      //3.
      async saveUserInfo(){
        //3.1 
        let result=await changeUserInfo(this.userInfo.id,this.user_name, this.user_sex, this.user_address, this.user_birthday,
          this.user_sign);
        //3.2
        Toast({
          message:result.message,
          position:'bottom',
          duration:2000
        });
        //3.3
        if(result.success_code===200){
          //3.4
          this.$store.dispatch('getUserInfo');
          //3.5
          setTimeout(()=>{
            this.$router.replace('/me');
          },2000)
        }
      }
    }
  }
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
  .user-detail
    width 100%
    height 100%
    .user-detail-top
      width 100%
      height 60px
      line-height 60px
      padding-left 10px
      font-weight bold
    .user-detail-group
      .user-icon
        height 60px
        padding 0 10px
        background-color #fff
        border-bottom: 1px solid #e0e0e0
        display flex
        justify-content space-between
        align-items center
        img
          width 50px
          border-radius 50%
      .user-item
        height 40px
        padding 0 10px
        background-color #fff
        border-bottom: 1px solid #e0e0e0
        display flex
        justify-content space-between
        align-items center
        input
          border none
          outline none
          text-align right
      button
        width 90%
        height 40px
        line-height 40px
        background-color #e9232c
        text-align center
        margin 20px 5%
        border none
        font-size 16px
        color #fff
        border-radius 10px
    .right-title-color
      color #999
      font-size 14px
</style>



