<template>
  <div class="hot">
    <!--1.-->
    <div class="swiper-container" v-if="homecasual.length > 0">
      <div class="swiper-wrapper">
        <div class="swiper-slide" v-for="(casual, index) in homecasual" :key="index">
          <img :src="casual.imgurl" alt="" width="100%"></div>
      </div>
      <!--  -->
      <div class="swiper-pagination"></div>
    </div>
    <!--2.-->
    <hot-nav/>
    <!--3.-->
    <div class="hot-ad">
      <img src="./../../imgs/hot_ad/home_ad.gif" alt="" width="100%">
    </div>
    <!--4.-->
    <hot-shop-list/>
  </div>
</template>
<script>
  import Swiper from 'swiper'
  import 'swiper/dist/css/swiper.min.css'

  import HotNav from  './HotNav'
  import HotShopList from './HotSopList'

  import {
    mapState
  } from 'vuex'

  export default {
    name: "Hot",
    components:{
      HotNav,
      HotShopList
    },
    computed:{
      ...mapState(['homecasual'])
    },
    mounted() {
      //1.
      this.$store.dispatch('reqHomeCasual');
      //2.
      this.$store.dispatch('reqHomeNav');
      //3.
      this.$store.dispatch('reqHomeShopList');
    },
    watch:{
      homecasual(){
        this.$nextTick (()=>{
          //swiper
          new Swiper ('.swiper-container', {
            autoplay:true,//
            loop: true, // 
            // 
            pagination: {
              el: '.swiper-pagination',
            },
          })
        })
      }

    }
  }
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
  .hot
    width 100%
    height 100%
    background #f5f5f5
    padding-top: 46px
    .hot-ad
      background-color: #fff
      margin 8px 0
      padding: 5px


</style>



