<template>
  <div class="search">
    <!---->
    <search-nav/>
    <div class="shop">
      <!---->
      <div class="menu-wrapper">
        <ul>
          <!--current-->
          <li
            class="menu-item"
            v-for="(goods,index) in searchgoods"
            :key="index"
            :class="{current:index===currentIndex}"
            @click="clickLeftItem(index)"
            ref="menulist"
          >
            <span>{{goods.name}}</span>
          </li>
        </ul>
      </div>
      <!---->
      <div class="shop-wrapper">
        <ul ref="shopsParent">
          <li class="shops-li" v-for="(goods,index1) in searchgoods" :key="index1">
            <div class="shop-title">
              <h4>{{goods.name}}</h4>
              <a href=""></a>
            </div>
            <ul class="phone-type" v-if="goods.tag==='phone'">
              <li v-for="(phone,index) in goods.category" :key="index">
                <img :src="phone.icon" alt="">
              </li>
            </ul>
            <ul class="shops-items">
              <li v-for="(item,index2) in goods.items" :key="index2">
                <img :src="item.icon" alt="">
                <span>{{item.title}}</span>
              </li>
            </ul>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import SearchNav from './children/SearchNav'
  import {mapState} from 'vuex'
  import BScroll from 'better-scroll'

  export default {
    name: "Search",
    data() {
      return {
        scrollY: 0, // Y()
        rightLiTops: [], // 
      }
    },
    mounted() {
      this.$store.dispatch('reqSearchGoods')
    },
    computed: {
      ...mapState(['searchgoods']),
      // 1.
      currentIndex() {
        //1.1
        const {scrollY, rightLiTops} = this;
        //1.2
        return rightLiTops.findIndex((LiTop, index) => {
          this._leftScroll(index);
          return scrollY >= LiTop && scrollY < rightLiTops[index + 1]
        })
      }
    },
    components: {
      SearchNav
    },
    watch: {
      searchgoods() {
        this.$nextTick(() => {
          //1.1
          this._initBScroll();
          //1.2
          this._initRightTops()
        })
      }
    },
    methods: {
      //1.1
      _initBScroll() {
        //1.1
        this.leftScroll = new BScroll('.menu-wrapper', {});
        //1.2
        this.rightScroll = new BScroll('.shop-wrapper', {
          probeType: 3
        });
        //1.3
        this.rightScroll.on('scroll', (pos) => {
          this.scrollY = Math.abs(pos.y);
        });
      },

      //1.2
      _initRightTops() {
        //1.2.1
        const tempArray = [];
        //1.2.2
        let top = 0;
        tempArray.push(top);
        //1.2.3li 
        let allLis = this.$refs.shopsParent.getElementsByClassName('shops-li');
        //
        Array.prototype.slice.call(allLis).forEach(li => {
          top += li.clientHeight;
          tempArray.push(top);
        });
        //1.2.4
        this.rightLiTops = tempArray


      },

      // 1.3  
      clickLeftItem(index) {
        this.scrollY = this.rightLiTops[index];
        this.rightScroll.scrollTo(0, -this.scrollY, 300);
      },
      //1.4
      _leftScroll(index) {
        let menuLists = this.$refs.menulist;
        let el = menuLists[index];
        this.leftScroll.scrollToElement(el, 300, 0, -100)
      },

    }
  }

</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
  @import "../../common/stylus/mixins.styl"
  .search
    background #F5F5F5
    width 100%
    height 100%
    overflow: hidden

  .shop
    display: flex
    position: absolute
    top: 60px
    bottom: 50px
    width: 100%
    overflow: hidden

    .menu-wrapper
      background-color: #e0e0e0
      width: 80px
      flex: 0 0 80px

      .menu-item
        width: 100%
        height: 60px
        background-color: #fafafa
        display: flex
        justify-content: center
        align-items: center
        font-weight: lighter
        color: #666666
        position: relative

      .current
        color #e02e24


      .current::before
        content: ''
        background-color: #e02e24
        width: 4px
        height: 30px
        position: absolute
        left: 0

    .shop-wrapper
      flex 1
      background-color: #fff

      .shop-title
        display: flex
        flex-direction: row
        padding: 0 10px
        height: 44px
        align-items: center
        justify-content: space-between
        color: #999;

        a
          color: #999;
          text-decoration: none
          font-weight: lighter

      .shops-items
        display: flex
        flex-wrap wrap

        li
          display: flex
          flex-direction: column
          width: 33.3%
          height: 90px
          justify-content: center
          align-items: center
          color: #666;
          font-size: 14px
          font-weight: lighter

          img
            width: 60%
            height: 60%
            margin-bottom: 5px

      .phone-type
        width: 100%
        display: flex
        flex-direction: row
        flex-wrap wrap
        border-bottom-1px(#ccc)

        li
          width 33.3%
          margin: 5px 0;

          img
            width: 70%;
            display: flex
            justify-content: center
            align-items: center
</style>



