<template>
  <div class="me-setting">
    <div>
      <me-common-cell left-icon="itlike-3" left-title=""/>
      <me-common-cell left-icon="itlike-2" left-title=""/>
      <me-common-cell left-icon="itlike-1" left-title="" right-title="" right-title-color="red"/>
    </div>

    <div style="margin-top: 10px">
      <me-common-cell left-icon="itlike-4" left-title="" :clickCell="dealClickCell"/>
      <me-common-cell left-icon="itlike-5" left-title="itlike" :clickCell="dealClickCell"/>
    </div>

    <div style="margin-top: 10px">
      <me-common-cell left-icon="itlike-1" left-title=""/>
    </div>

    <div class="logout" @click="dealLogout"></div>
  </div>
</template>

<script>
    import MeCommonCell from './MeCommonCell';
    import {Toast,MessageBox} from 'mint-ui';
    import {mapActions} from 'vuex'
    export default {
        name: "MeSetting",
        components:{
          MeCommonCell
        },
        methods:{
          ...mapActions(['logOut']),
          dealClickCell(props){
            Toast(''+props);
          },
          dealLogout(){
            MessageBox.confirm('?').then(action => {
              if('confirm'===action){
                //
                let result=this.logOut({});
                console.log(result);
                //
                this.$router.replace('/home')
              }
            });
          }
        }
    }
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
  .logout
    width: 100%
    height: 44px
    margin-top: 10px
    background-color: #fff
    display: flex
    justify-content: center
    align-items: center
    text-align: center
</style>



