<template>
  <div class="me">
 <!--   <div v-if="userInfo.id">
      <me-top/>
    </div>-->
    <div>
      <me-top/>
    </div>
    <!--<select-login/>-->
  </div>
</template>

<script>

    import SelectLogin from './../Login/SelectLogin'
    import MeTop from './MeTop'
    import {mapState} from  'vuex'
    export default {
        name: "Me",
        computed:{
          ...mapState(['userInfo'])
        },
        components:{
          MeTop,
          SelectLogin,
