<template>
  <div class="recommend-container" v-if="recommendshoplist.length>0">
      <ul class="recommend">
        <shop-list v-for="(item,index) in recommendshoplist"
                   tag="li"
                  :item=item
                  :key="index"/>
      </ul>
  </div>
</template>

<script>
  import {mapState} from 'vuex'
  import ShopList from './../../components/ShopList/ShopList'
    export default {
        name: "Recommend",
        mounted(){
          this.$store.dispatch('reqRecommendShopList')
        },
      computed:{
          ...mapState(['recommendshoplist'])
      },
      components:{
          ShopList
      }
    }
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
  .recommend-container
    background:#f5f5f5
    width 100%
    height 100%
    .recommend
      display: flex
      flex-direction: row
      flex-wrap wrap
      background:#f5f5f5
      margin-bottom: 50px
      .recommend-item:nth-child(2n-1)
        margin-right: 1%



</style>



