<template>
  <div class="login-container">
    <!---->
    <div class="login-inner">
      <!---->
      <div class="login-header">
        <div class="login-logo">
          <img src="./images/lk_logo_sm.png" alt="" width="250">
        </div>
        <!---->
        <div class="login-header-title">
          <a href="javascript:;" :class="{current:loginMode}" @click="dealLoginMode(true)"></a>
          <a href="javascript:;" :class="{current:!loginMode}" @click="dealLoginMode(false)"></a>
        </div>
      </div>
      <!---->
      <div class="login-content">
        <form>
          <!---->
          <div :class="{current:loginMode}">
            <section class="login-message">
              <input type="tel" maxlength="11" placeholder="" v-model="phone">
              <button v-if="!countDown"
                      class="get-verification"
                      :class="{phone_right:phoneRight}"
                      @click.prevent="getVerifyCode()"
              >
              </button>
              <button disabled="disabled" class="get-verification" v-else>({{countDown}}s)</button>

            </section>
            <section class="login-verification">
              <input type="tel" maxlength="8" placeholder="" v-model="code">
            </section>
            <section class="login-hint">
              
              <a href="javascript:;"></a>
            </section>
          </div>


          <!---->
          <div :class="{current:!loginMode}">
            <section>
              <section class="login-message">
                <input type="tel" maxlength="11" placeholder="//" v-model="user_name">
              </section>
              <section class="login-verification">
                <input type="password" maxlength="18" placeholder="" v-if="pwdMode" v-model="pwd">
                <input type="text" maxlength="18" placeholder="" v-else v-model="pwd">
                <div class="switch-show">
                  <img @click.prevent="dealPwdMode(false)" :class="{on:pwdMode}" src="./images/hide_pwd.png" alt=""
                       width="20">
                  <img @click.prevent="dealPwdMode(true)" :class="{on:!pwdMode}" src="./images/show_pwd.png" alt=""
                       width="20">
                </div>
              </section>
              <section class="login_message">
                <input type="text" maxlength="11" placeholder="" v-model="captcha">
                <img class="get_verification"
                     src="http://127.0.0.1:3000/api/captcha"
                     alt="captcha"
                     ref="captcha"
                     @click.prevent="getCaptcha()"
                >
              </section>
            </section>
          </div>
          <button class="login-submit" @click.prevent="login()"></button>
        </form>
        <button class="login-back" @click="$router.back()"></button>
      </div>
    </div>
  </div>
</template>

<script>
  import {getPhoneCode, phoneCodeLogin, pwdLogin} from './../../api/index';
  import {Toast} from 'mint-ui';
  import {mapActions} from 'vuex'

  export default {
    // name: "Login",
    data() {
      return {
        loginMode: true,//,true false,
        phone: '',
        code: '',
        countDown: 0,//
        pwdMode: true,// true  false 
        pwd: '',//
        userInfo: {},//
        user_name: '',//
        captcha: '',//
      }
    },
    computed: {
      //
      phoneRight() {
        return /^[1][3,4,5,7,8][0-9]{9}$/.test(this.phone)
      }
    },
    methods: {
      ...mapActions(['syncUserInfo']),
      //1.
      dealLoginMode(flag) {
        this.loginMode = flag;
      },
      //2.
      async getVerifyCode() {
        //2.1
        if (this.phoneRight) {
          this.countDown = 60;
          this.intervalId = setInterval(() => {
            this.countDown--;
            if (this.countDown === 0) {
              clearInterval(this.intervalId)
            }
          }, 1000)
        }
        //2.2
        let result = await getPhoneCode(this.phone);
        console.log(result);
        //2.3
        if (result.err_code === 0) {
          //
          Toast({
            message: result.message,
            position: 'center',
            duration: 2000
          });
          /*  //
            clearInterval(this.intervalId);
            this.countDown = 0*/
        }
      },
      //3.
      dealPwdMode(flag) {
        this.pwdMode = flag;
      },
      //4.
      getCaptcha() {
        this.$refs.captcha.src = 'http://127.0.0.1:3000/api/captcha?time=' + new Date();
      },
      //5.
      async login() {
        if (this.loginMode) {//
          //5.1
          if (!this.phone) {
            Toast("!");
            return;
          } else if (!this.phoneRight) {
            Toast("!");
            return;
          }
          if (!this.code) {
            Toast("!");
            return;
          } else if (!(/^\d{6}$/gi.test(this.code))) {
            Toast("!");
            return;
          }
          //5.2
          const result = await phoneCodeLogin(this.phone, this.code);
          if(result.success_code === 200) {
            this.userInfo = result.message;
          } else {
            this.userInfo = {
              message: ', '
            }
          }
          // console.log(this.userInfo);
        } else {//
          //5.3
          if (!this.user_name) {
            Toast("//!");
            return;
          } else if (!this.pwd) {
            Toast("!");
            return;
          } else if (!this.captcha) {
            Toast("!");
            return;
          }
          //5.4
          const result = await pwdLogin(this.user_name, this.pwd, this.captcha);

          if(result.success_code === 200) {
            this.userInfo = result.message
          } else {
            this.userInfo = {
              message: ''
            }
          }
        }
        //6.
        if (!this.userInfo.id) {//
          Toast(this.userInfo.message);
        } else {//
          //6.1,state
          this.syncUserInfo(this.userInfo);
          //6.2
          this.$router.back();
        }
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixins.styl"
  .login-container
    width 100%
    height 100%
    background #fff

    .login-inner
      padding-top 60px
      width 80%
      margin 0 auto

      .login-header
        .login-logo
          font-size 40px
          font-weight bold
          color mediumpurple
          text-align center

        .login-header-title
          padding-top 40px
          padding-bottom 10px
          text-align center

          > a
            color #333
            font-size 14px
            padding-bottom 4px

            &:first-child
              margin-right 40px

            &.current
              color mediumpurple
              font-weight 700
              border-bottom 2px solid mediumpurple

      .login-content
        > form
          > div
            display none

            &.current
              display block

            input
              width 100%
              height 48px
              padding-left 8px
              box-sizing border-box
              border 1px solid #ddd
              border-radius 4px
              outline 0
              font 400 14px Arial

              &:focus
                border 1px solid mediumpurple

            .login-message
              position relative
              margin-top 16px
              height 48px
              font-size 14px
              background #fff

              .get-verification
                position absolute
                top 50%
                right 10px
                transform translateY(-50%)
                border 0
                color #ccc
                font-size 14px
                background transparent

                &.phone_right
                  color purple

            .login-verification
              position relative
              margin-top 16px
              height 48px
              font-size 14px
              background #fff

              .switch-show
                position absolute
                right 10px
                top 12px

                img
                  display none

                img.on
                  display block

            .login-hint
              margin-top 12px
              color #999
              font-size 12px
              line-height 20px

              > a
                color mediumpurple

          .login-submit
            display block
            width 100%
            height 42px
            margin-top 30px
            border-radius 4px
            background mediumpurple
            color #fff
            text-align center
            font-size 16px
            line-height 42px
            border 0

        .login-back
          display block
          width 100%
          height 42px
          margin-top 15px
          border-radius 4px
          background transparent
          border: 1px solid mediumpurple
          color mediumpurple
          text-align center
          font-size 16px
          line-height 42px
</style>



