<template>
    <div class="home">
      <ly-tab
        v-model="selectedId"
        :items="items"
        :options="options"
        @change="handleChange"
        class="fix"
      />
      <router-view></router-view>
    </div>

</template>

<script>
    export default {
        name: "home",
        data(){
          return{
            selectedId:0,//id
            items:[
              {label: ''},
              {label: ''},
              {label: ''},
              {label: ''},
              {label: ''},
              {label: ''},
              {label: ''},
              {label: ''},
              {label: ''}
            ],
            options:{
              activeColor:'#e9232c',//
            },
            //
            subRouteUrl:[
              '/home/hot', '/home/dress','/home/box', '/home/mbaby','/home/general', '/home/food',
               '/home/shirt', '/home/man','/home/ele',
            ]
          }
        },
        methods:{
          handleChange(item,index){
            this.$router.replace(this.subRouteUrl[index])
          }
        }
    }
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
  .home
    background: #f5f5f5
    width 100%
    height 100%
    .fix
      position: fixed
      left: 0
      top: 0
      z-index: 998
</style>



