<template>
  <div class="me-top">
    <router-link tag="div" class="user" to="/userdetail">
      <img src="./images/me_icon.png" alt="">
      <p>{{userInfo.user_phone | phoneFormat}}</p>
      <i class="itlike-3"></i>
    </router-link>
    <div class="my-older">
      <div class="older-top">
        <h3></h3>
        <span> > </span>
      </div>
      <div class="older-bottom">
        <div class="bottom-item">
          <i class="itlike-1"></i>
          <span></span>
        </div>
        <div class="bottom-item">
          <i class="itlike-2"></i>
          <span></span>
        </div>
        <div class="bottom-item">
          <i class="itlike-3"></i>
          <span></span>
        </div>
        <div class="bottom-item">
          <i class="itlike-4"></i>
          <span></span>
        </div>
        <div class="bottom-item">
          <i class="itlike-5"></i>
          <span></span>
        </div>
      </div>
    </div>
    <div class="setting">
      <div class="setting-item">
        <i class="itlike-1"></i>
        <span></span>
      </div>
      <div class="setting-item">
        <i class="itlike-2"></i>
        <span></span>
      </div>
      <div class="setting-item">
        <i class="itlike-3"></i>
        <span></span>
      </div>
      <div class="setting-item">
        <i class="itlike-4"></i>
        <span></span>
      </div>
      <div class="setting-item">
        <i class="itlike-5"></i>
        <span></span>
      </div>
      <div class="setting-item">
        <i class="itlike-uniE902"></i>
        <span></span>
      </div>
      <div class="setting-item">
        <i class="itlike-1"></i>
        <span></span>
      </div>
      <div class="setting-item">
        <i class="itlike-3"></i>
        <span></span>
      </div>
      <router-link tag="div" class="setting-item" to="/setting">
        <i class="itlike-4"></i>
        <span></span>
      </router-link>
    </div>
  </div>
</template>

<script>
    import {mapState} from 'vuex'
    export default {
        name: "MeTop",
        computed:{
          ...mapState(["userInfo"])
        },
        filters:{
           phoneFormat(phone){
              // 1. 
             let phoneArr = [...phone];
             // console.log(phoneArr);
             let str = '';
             phoneArr.forEach((item, index)=>{
                if(index === 3 || index === 4 || index === 5 || index === 6){
                    str += '*'
                }else {
                   str += item;
                }
             });
             return str
           }
        }
    }
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
  .me-top
    font-size 14px
    .user
      display flex
      flex-direction row
      align-items center
      padding 20px
      background-color #fff
      margin-bottom 10px
      p
        margin 0 10px
      img
        width 60px
        height 60px
        border-radius 50%
      i
        font-size 25px
    .my-older
      background-color #fff
      .older-top
        display flex
        flex-direction row
        padding 0 10px
        justify-content space-between
        height 44px
        line-height 44px
      .older-bottom
        display flex
        .bottom-item
          flex 1
          height 60px
          display flex
          flex-direction column
          justify-content center
          align-items center
          i
            font-size 30px
            color #E9232C
            margin-bottom 5px
    .setting
      margin-top 10px
      background-color #fff
      display flex
      justify-content space-between
      flex-wrap wrap
      .setting-item
        width 90px
        height 70px
        display flex
        flex-direction column
        justify-content center
        align-items center
        i
          font-size 30px
          color orange
          margin-bottom 5px
</style>



